<!-- 个人中心 -->
<template>
	<view v-if="!isFirstload" class="my-info">
		<!-- 顶部背景 -->
		<view class='my-info-bg pt30' :style="'background-image: url(' + pic[topBackGroupImageIndex].link + ');margin-top:' + CustomBar + 'px;'">
				<!-- 已登录 -->
                <view v-if="isLogin">
					<view style="overflow: hidden;">
						<avatar-image :width="150" />
					</view>
				</view>
                <!-- 未登录 -->
                <view v-else @click="handleLogin" class="my-info-bg__nologin">
					<view style="overflow: hidden; mb20">
						<avatar-image :width="150" />
					</view>
					<view class="padding text-red text-bold">
						未登录，请点击登录账号
					</view>
				</view>

			<image class="my-info-bg__img" src='https://cdn.nlark.com/yuque/0/2019/gif/280373/1570670848649-assets/web-upload/3dbaa72a-062b-470f-9b9d-058ff8f85ab8.gif'
			 mode='scaleToFill'></image>
		</view>

        <!-- 学生信息 -->
		<block>
			<view class="my-info-student pa30">
				<view class="my-info-student__item solid-right animation-slide-bottom">
					<view class="text-xxl text-orange">{{ isLogin ? userInfo.userName : '--' }}</view>
					<view class="margin-top-sm">
					    姓名</view>
				</view>
				<view class='my-info-student__item solid-right animation-slide-bottom'>
					<view class="text-xxl text-blue">{{ isLogin ? userInfo.userClass : '--' }}</view>
					<view class="margin-top-sm">
						班级</view>
				</view>
				<view class='my-info-student__item animation-slide-bottom'>
					<view class="text-xxl text-red">{{ isLogin ? userInfo.userNum : '--' }}</view>
					<view class="margin-top-sm">
						学号</view>
				</view>
			</view>

            <!-- 工具 -->
			<view class="my-info-tool">

				
				<view class="my-info-tool-item animation-slide-top">
					<view class="my-info-tool-item__btn mh20" @click="toInfoView">
						<image class="my-info-tool-item__img mr10" src='../../static/me/icon/bianqian.png' mode='aspectFit'></image>
						<text class='my-info-tool-item__person'>个人资料</text>
					</view>
				</view>
                <view class="my-info-tool-item animation-slide-top">
					<view class="my-info-tool-item__btn mh20" open-type="share">
						<image class="my-info-tool-item__img mr10" src='../../static/me/icon/lvhang.png' mode='aspectFit'></image>
						<text class='my-info-tool-item__person'>分享小程序</text>
					</view>
				</view>
				
				
			</view>


            <!-- 其他工具 -->

            <view class="my-info-tool">
                <view class="my-info-tool-item ph30 animation-slide-bottom" @click="callPhoneNumber">
					<view class="my-info-tool-item__btn mh20" data-number="13111182031">
                        <image class="my-info-tool-item__img mr10" src='../../static/me/icon/dengta.png' mode='aspectFit'></image>
						<text class='my-info-tool-item__person'>技术支持</text>
                        
					</view>
                    <view class="action pr20">
						<view class="cu-tag round bg-blue light">13111182031</view>
					</view>
				</view>

                <view class="my-info-tool-item ph30 animation-slide-bottom">
					<view class="my-info-tool-item__btn mh20" open-type="feedback">
						<image class="my-info-tool-item__img mr10" src='../../static/me/icon/chucuo.png' mode='aspectFit'></image>
						<text class='text-lg margin-sm'>问题反馈</text>
					</view>
				</view>
                <view class="my-info-tool-item ph30 animation-slide-bottom">
					<view class="my-info-tool-item__btn mh20" @click="toAboutOut">
						<image class="my-info-tool-item__img mr10" src='../../static/me/icon/xiaoxi.png' mode='aspectFit'></image>
						<text class='text-lg margin-sm'>关于我们</text>
					</view>
				</view>
			</view>

            <view class="my-info-tool" v-if="isLogin">
                <view class="my-info-tool-item ph30 animation-slide-bottom">
					<view class="my-info-tool-item__btn mh20" @click="handleLogout">
						<image class="my-info-tool-item__img mr10" src='../../static/me/icon/xiaoxi.png' mode='aspectFit'></image>
						<text class='text-lg margin-sm'>退出登录</text>
					</view>
				</view>
            </view>

		</block>
		
		<view style="height: 200rpx;width: 1rpx;"></view>
	</view>
</template>

<script>
import store from '@/store'
import AvatarImage from '@/components/AvatarImage';
import * as UserApi from '@/api/user'
import { checkLogin } from '@/utils/app';
	export default {
        components: {
            AvatarImage
        },
		data() {
			return {
                // 主题颜色
				pic: [{
					link: 'https://cdn.zhoukaiwen.com/zjx_me_bg1.jpeg',
					name: '春天'
				}, {
					link: 'https://cdn.zhoukaiwen.com/zjx_me_bg2.jpeg',
					name: '夏天'
				}, {
					link: 'https://cdn.zhoukaiwen.com/zjx_me_bg3.jpeg',
					name: '秋天'
				}, {
					link: 'https://cdn.zhoukaiwen.com/zjx_me_bg4.jpeg',
					name: '冬天'
				}, {
					link: 'https://cdn.zhoukaiwen.com/zjx_me_bg5.jpeg',
					name: '幽静'
				}, {
					link: 'https://cdn.zhoukaiwen.com/zjx_me_bg6.jpg',
					name: '天空'
				}],
				topBackGroupImageIndex: 4,
                // 正在加载
                isLoading: true,
                // 首次加载
                isFirstload: false,
                // 是否已登录
                isLogin: false,
                // 当前用户信息
                userInfo: {},
                // 学生信息
                studentInfo: {nickname: '--', xueyuan: '--', class: '--'}
			}
		},

		/**
         * 声命周期--监听页面显示
         */
        created() {
            this.onRefreshPage();
        },

        /**
         * 分享
         */
        onShareAppMessage(res) {
            return {
                title: '看看这个小程序多好玩～',
            };
        },
        
        methods: {

            /**
             * 刷新页面
             */
            onRefreshPage() {
                // 判断是否已经登录
                this.isLogin = checkLogin();
                // 获取页面数据
                this.getPageData();
            },
            
            /**
             * 获取当前用户信息
             */
            getUserInfo() {
                return new Promise((resolve, reject) => {
                    !this.isLogin ? resolve(null) : UserApi.getUserInfo({}, { load: this.isFirstload })
                    .then(result => {
                        this.userInfo = result.data.qskInfo
                        resolve(this.userInfo)
                    })
                    .catch(err => {
                        this.isLogin = false
                        reject(err)
                    })
                })
            },

            /**
             * 获取页面数据
             */
            getPageData(callback) {
                this.isLoading = true
                this.getUserInfo()
                .then(() => {
                    this.isFirstload = false
                    callback && callback()
                })
                .catch(err => console.log('catch', err))
                .finally(() => this.isLoading = false)
            },

            /**
             * 去用户信息页面
             */
            toInfoView() {
                uni.navigateTo({
					url: '/pages/user/userdetail'
				})
            },

            /**
             * 去关于我们
             */
            toAboutOut() {
                uni.navigateTo({
					url: '/pages/user/about'
				})
            },

            /**
             * 技术支持
             */
            callPhoneNumber() {
                uni.makePhoneCall({
					phoneNumber: "13111182031",
				});
            },

            /**
             * 跳转登录页面--进行登录
             */
            handleLogin() {
                !this.isLogin && this.$navTo('pages/login/login')
            },

            /**
             * 退出登录
             */
            handleLogout() {
                const that = this
                uni.showModal({
                    title: '友情提示',
                    content: '您确定要退出登录吗?',
                    success(res) {
                        if (res.confirm) {
                            store.dispatch('users/logout', {})
                            .then(result => that.onRefreshPage())
                        }
                    }
                })
            }
        },

        /**
         * 下拉刷新
         */
        onPullDownRefresh() {
            this.getPageData(() => {
                uni.stopPullDownRefresh()
            })
        }
	}
</script>

<style lang="scss">
	@import "@/styles/tools/index.scss";
    @import "@/styles/settings/var.scss";

	@include b(my-info-bg) {
        position: relative;
		background: $background-color-secondary;  // 1
		background-size: 100% 100%;
		height: 550rpx;
        @include box-center($justify:center, $align: center);
		overflow: hidden;  // 
		color: $color-text-tertiary;   //
		font-weight: 300; 
		text-shadow: 0 0 3px $color-transparent-black;   

        @include e(nologin) {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
		
		@include e(img) {
			position: absolute;
            width: 100%;
            bottom: 0;
            left: 0;
            z-index: 99;
            mix-blend-mode: screen;
            height: 100rpx;
		}
	}

    @include b(my-info-student) {
        /* padding: 30upx; */
        display: flex;
        text-align: center;
        color: #8799a3;
        background-color: $background-color-secondary;
        position: relative;
        box-shadow: 0 10rpx 10rpx $color-transparent-black;

        @include e(item) {
            @include grid($flex-direction: column);
            flex: 1;
        }
    }

    @include b(my-info-tool) {
        margin: 30rpx 30rpx 20rpx 30rpx;
        border-radius: 20rpx;
        box-shadow: 0rpx 0rpx 90rpx 0rpx $color-transparent-black;
        color: $color-text-secondary;
        overflow: hidden;
    }
    @include b(my-info-tool-item) {
        position: relative;
        @include box-center($justify: space-between);
        min-height: 100upx;
        background-color: $background-color-secondary;
        @include e(btn) {
            width: 100%;
            @include box-center($justify:flex-start, $align: center);
            font-size: 28rpx;
            height: 64rpx;
            line-height: 1;
            text-align: center;
            padding: 0;
        }
        @include e(img) {
            width: 1.6em;
            height: 1.6em;
        }
    }

    .my-info-tool>.my-info-tool-item:after {
        position: absolute;
        top: 0;
        left: 0;
        box-sizing: border-box;
        width: 200%;
        height: 200%;
        border-bottom: 1upx solid $background-color-primary;
        border-radius: inherit;
        content: " ";
        transform: scale(.5);
        transform-origin: 0 0;
        pointer-events: none
    }
    .solid-right {
        border-right: 1rpx solid rgba(0, 0, 0, .1);
    }


    .text-blue {
        color: #0081ff;
    }


    .text-orange {
        color: #f37b1d;
    }


    .text-red {
        color: #e54d42;
    }
</style>
